/* ------------------------------------------------------------ *\
    Form-radio-selection
\* ------------------------------------------------------------ */

.radio-selection {
    position: relative;
    width: 300px;
    margin: 0 auto;
    & ul {
        width: 300px;
        margin: 0 auto;
        & li {
            // display: block;
            // position: relative;
            color: $gray;
            & input[type="radio"] {
                position: absolute;
            }
            & label {
                position: relative;
                padding-left: 28px;
                cursor: pointer;
                line-height: 20px;
                display: inline-block;
                // font-size: 14px;
            }
            & .check {
                display: block;
                position: absolute;
                border: 1px solid $white;
                background: $white;
                border-radius: 100%;
                height: 12px;
                width: 12px;
                top: 11px;
                left: 20px;
                &::before {
                    display: block;
                    position: absolute;
                    content: '';
                    border-radius: 100%;
                    height: 8px;
                    width: 8px;
                    top: 1px;
                    left: 1px;
                    margin: 0;
                }
            }
        }
    }
}

@media #{$smartphone} {
    .radio-selection {
        width: 400px;
        margin: 0 auto;
        &ul {
            width: 350px;
            margin: 0 auto;
            & label {
                font-size: 14px;
            }
        }
    }
}

